<template>
    <div class="header">
        <div class="header-nav">
            <div class="header-main-left">
                <img class="header-logo" src="/images/common/beaver.png"></img>
                <h1 class="header-title"><span>让小产品的独立变现更简单</span></h1>
            </div>
            <div class="header-main-right">
                <div class="header-menu" style="cursor: pointer" @click="joinClick">加入变现星球</div>
                <div class="header-menu" style="cursor: pointer" @click="commit">我有推荐</div>
            </div>
        </div>
        <Dialog :visible.sync="dialogZSXQVisible" top="20px" class="zsxq-dialog">
            <div class="zsxq-container">
                <div>
                    <span style="font-weight: bold">「IndieMaker独立变现」- 让小产品的独立变现更简单</span>，在这里分享做小产品的方法论、工具、资料，遇见理念相同的同类。做出自己的产品，持续变现。加入星球，可3天无理由退出。
                </div>
                <img class="zsxq-img" src="/images/common/zsxq.jpg">
            </div>
        </Dialog>
        <Dialog :visible.sync="dialogRecommendVisible" top="20px" class="zsxq-dialog">
            <div class="zsxq-container">
                <div style="font-weight: bold">输入推荐链接，我会尽快整理添加，谢谢推荐:)</div>
                <div>
                    <input class="recommend—input" v-model="recommendLink"></input>
                </div>
                <div class="recommend—btn" @click="commitLink">
                    推荐
                </div>
            </div>
        </Dialog>
    </div>
</template>

<script>
    import Vue from 'vue'
    import {Button, Row, Col, Dialog, Notification} from 'element-ui';
    Vue.prototype.$notify = Notification;

    export default {
        name: 'Header',
        components: {
            'el-row': Row,
            'el-col': Col,
            Dialog
        },
        props: {
            msg: String
        },
        data() {
            return {
                dialogZSXQVisible: false,
                dialogRecommendVisible: false,
                recommendLink: ''
            }
        },
        methods: {
            joinClick() {
                this.dialogZSXQVisible = true
                gtag('event', 'join_open', {
                    'event_category': 'home'
                })
            },
            commit() {
                this.dialogRecommendVisible = true
                gtag('event', 'commit', {
                    'event_category': 'home'
                })
            },
            commitLink() {
                if (this.recommendLink) {
                    this.$notify({
                        title: '成功',
                        message: '感谢你的分享！',
                        type: 'success'
                    });
                    gtag('event', 'commit', {
                        'event_category': 'home',
                        'event_label': this.recommendLink,
                    })
                    this.dialogRecommendVisible = false
                }
            }
        }
    }
</script>

